import React from 'react'
import { Link } from 'gatsby'
import { navigateTo } from "gatsby-link";

import Layout from '../components/layout'
import SEO from '../components/seo'

import contact from '../images/contact.svg';

function encode(data) {
  return Object.keys(data)
    .map(key => encodeURIComponent(key) + "=" + encodeURIComponent(data[key]))
    .join("&");
}

export default class Contact extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  handleChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  handleSubmit = e => {
    e.preventDefault();
    const form = e.target;
    fetch("/", {
      method: "POST",
      headers: { "Content-Type": "application/x-www-form-urlencoded" },
      body: encode({
        "form-name": form.getAttribute("name"),
        ...this.state
      })
    })
      .then(() => navigateTo(form.getAttribute("action")))
      .catch(error => alert(error));
  };

  render() {
    return (
        <Layout>
        <SEO title="Contact" />
          <div className="contactForm">
            <div className="aboveForm">
                <div style={{textAlign: `center`,}}>
                    <img style={{maxWidth: `300px`, marginTop: `20px`,}} src={contact} alt="Contact me for Web Design needs" />
                </div>
                <div>
                    <h2>Contact me</h2>
                    <p style={{fontWeight: `bold`,}}>Whether you want to contact me about building you a site, collaborating, or you want to learn more about 
            web design and/or development, don't hesitate to drop me a message!</p>
                </div>
            </div>
            <form
              name="contact"
              method="post"
              action="/thanks/"
              data-netlify="true"
              data-netlify-honeypot="bot-field"
              onSubmit={this.handleSubmit}
            >
              {/* The `form-name` hidden field is required to support form submissions without JavaScript */}
              <input type="hidden" name="form-name" value="contact" />
              <p hidden>
                <label>
                  Don’t fill this out:{" "}
                  <input name="bot-field" onChange={this.handleChange} />
                </label>
              </p>
              <div className="formName">
                <label>
                  Your name:<br />
                  <input type="text" name="name" onChange={this.handleChange} />
                </label>
              </div>
              <div className="formEmail">
                <label>
                  Your email:<br />
                  <input type="email" name="email" onChange={this.handleChange} />
                </label>
              </div>
              <div className="formReason">
                <label>
                  I need:<br />
                  <select onChange={this.handleChange}>
                    <option value="web">Web Design/Development</option>
                    <option value="seo">SEO/SEM Optimization</option>
                    <option value="maintenance">General Website Maintenance</option>
                    <option value="optimization">Website Optimization</option>
                    <option value="ssl">SSL Authentication</option>
                    <option value="general">General Inquiry</option>
                  </select>

                </label>
              </div>
              <div className="formMessage">
                <label>
                  Message:<br />
                  <textarea rows="5" name="message" onChange={this.handleChange} />
                </label>
              </div>
              <div className="formSubmit">
                <button type="submit">Send</button>
              </div>
            </form>
          </div>
        </Layout>
    );
  }
}